/***/ 8973:
/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Forty-Six, and One","date":"09 Feb 2008","slug":"/2008-02-09-forty-six-and-one/","tags":"Y2008"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2251628643\" title=\"View 'CIMG0460' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2254/2251628643_348ee4bb62.jpg\" alt=\"CIMG0460\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>Happy Birthday to Me! I turn forty-six today, and I started the day by working the closing shift in the restaurant last night, getting home at 5:30. It was a good shift despite being one closer short.</p>\n<p>It was also one year ago today that Frauke returned from one of her many business trips to Munich to tell that that she'd be given the opportunity to become a franchisee, and would be home a lot more in the future.</p>"},"next":{"fields":{"title":"Pardon My Silence","date":"08 Feb 2008","slug":"/2008-02-08-pardon-my-silence/"}},"previous":{"fields":{"title":"Valentine's Day","date":"14 Feb 2008","slug":"/2008-02-14-valentines-day/"}}},{"node":{"fields":{"title":"Pardon My Silence","date":"08 Feb 2008","slug":"/2008-02-08-pardon-my-silence/","tags":"Y2008"},"html":"<p>Forgive me for not posting an update on my brother here... I mentioned his status on <a href=\"http://twitter.com/papascott\">Twitter</a>, but most poeple would have missed that, I guess.</p>\n<p>He was released from the hospital last Tuesday, 29 January, a week after being admitted. With two stents freeing the arteries that caused the arrest, his heart should now be better off than before. If his heart were his only health problem, he could have returned to work this week... but instead he is resuming his chemotherapy this weekend.</p>\n<p>Emotionally, he's still a mess, angry and frustrated with God and the world. That's understandable, I guess, but not particularly constructive.</p>\n<p>I returned home also last Tuesday after 5 days in Minnesota. Frauke was able to handle both son and business alone for a week, and things are now normal again at home. We've had an overnight meeting at Westerland for the two of us, a short school vacation and a late birthday party for Christopher, and a small sickness wave at work. In other words, more of the usual and less of the unusual.</p>"},"next":{"fields":{"title":"At Least They Have Snakes","date":"08 Feb 2008","slug":"/2008-02-08-at-least-they-have-snakes/"}},"previous":{"fields":{"title":"Forty-Six, and One","date":"09 Feb 2008","slug":"/2008-02-09-forty-six-and-one/"}}},{"node":{"fields":{"title":"At Least They Have Snakes","date":"08 Feb 2008","slug":"/2008-02-08-at-least-they-have-snakes/","tags":"Y2008"},"html":"<p>Boing Boing Gadgets mentions the upcoming LEGO Indiana Jones video game and how (like the toys) it <a href=\"http://gadgets.boingboing.net/2008/02/08/lego-indiana-jones-v.html\">will not display Nazi symbols</a>. Good thing! As I mention in a <a href=\"http://gadgets.boingboing.net/2008/02/08/lego-indiana-jones-v.html#comment-121962\">comment</a>, we already had to have a talk with Christopher about the Nazi symbols in the films.  He's going have to deal with German history soon enough... he should be able to have fun with LEGO without having to be confronted with the symbols already.</p>\n<p>Of course it goes without saying that without the evil marketing geniuses at LEGO releasing toys and games in advance of the upcoming movie, Christopher wouldn't know about Indiana Jones at all, and it wouldn't be an issue.</p>"},"next":{"fields":{"title":"Indy!","date":"04 Feb 2008","slug":"/2008-02-04-indy/"}},"previous":{"fields":{"title":"Pardon My Silence","date":"08 Feb 2008","slug":"/2008-02-08-pardon-my-silence/"}}}],"pathPrefix":"","first":false,"last":false,"index":84,"pageCount":1101}}

/***/ })

